import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';

@Component({
    selector: 'app-sign-up',
    template: `
        <form (ngSubmit)="onSubmit();" [formGroup]="formSignUp">
            <input placeholder="Email" formControlName="email">
            <p *ngIf="formSignUp.get('email').invalid && formSignUp.get('email').touched">Email is require</p>
            <!-- <p *ngIf="formSignUp.controls.email.invalid && formSignUp.controls.email.touched">Email is require</p> -->
            <br><br>
            <input type="password" placeholder="Password" formControlName="password">
            <br><br>
            <div formGroupName="subject">
                <label><input type="checkbox" name="NodeJS" formControlName="nodeJS">NodeJS</label>
                <label><input type="checkbox" name="Angular" formControlName="angular">Angular</label>
                <label><input type="checkbox" name="ReactJS" formControlName="reactJS">ReactJS</label>
            </div>
            <br><br>
            <button [disabled]="formSignUp.invalid">Submit</button>
        </form>
        <code>{{ formSignUp.controls.email.errors | json }}</code>
    `,
})

export class SignUpComponent implements OnInit {
    
    formSignUp: FormGroup;
    
    // constructor() {
    //     this.formSignUp = new FormGroup({
    //         email: new FormControl('abc@gmail.com'),
    //         password: new FormControl(),
    //         subject: new FormGroup({
    //             nodeJS: new FormControl(true),
    //             angular: new FormControl(false),
    //             reactJS: new FormControl(true)
    //         })
    //     });
    // }

    constructor(private fb: FormBuilder) { }

    ngOnInit(): void {
        this.formSignUp = this.fb.group({
            email: ['', [Validators.email, formatGmailValidator]],
            password: '',
            subject: this.fb.group({
                nodeJS: true,
                angular: false,
                reactJS: true
            })
        });
    }

    onSubmit() {
        console.log(this.formSignUp.value)
    }

}

function formatGmailValidator(formControl: FormControl) {
    if(formControl.value.includes('@gmail.com')) {
        return null;
    }
    return {gmail: true};
}



