import { Component, ViewChild } from '@angular/core'
import { ChildComponent } from './child.component'

@Component({
    selector: 'app-perent',
    // template: `
    // <h2>Perent Component</h2>
    // <h3>{{ value }}</h3>
    // <app-child (clickAdd) = "changeValue($event);"></app-child>
    // `

    // template: ` // tự chạy được khi bỏ comment
    //     <button (click) = "child.value = child.value + 1">Add for child</button>
    //     <app-child #child></app-child>
    // `

    template: `
        <button (click) = "onAddForChild();">Add for child</button>
        <app-child></app-child>
    `
})

export class PerentComponent {
    // value = 0;

    // changeValue(isAdd: boolean) {
    //     if(isAdd) {
    //         this.value = this.value + 1;
    //     } else {
    //         this.value--;
    //     }
    // }

    @ViewChild(ChildComponent)
    myChild: ChildComponent;

    onAddForChild() {
        this.myChild.value++;
    }
}


