import { Injectable, MissingTranslationStrategy } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';

@Injectable()

export class WeatherService {
    constructor(private http: Http) {}

    getTemp(cityName: String) {
        const url = 'http://api.openweathermap.org/data/2.5/weather?appid=13ad1777f20b3bc3f4695b557fb7a53b&units=metric&q=' + cityName;
        return this.http.get(url)
        .toPromise()
        .then(res => res.json())
        .then(resJson => resJson.main.temp);
    }
}


