import { Component } from '@angular/core';
import { SignInService } from './sign-in.service';

@Component({
    selector: 'app-sign-in',
    template: `
        <form (ngSubmit)="onSubmit(formSignIn);" #formSignIn="ngForm">
        <!--<form (submit)="onSubmit(formSignIn);" #formSignIn="ngForm">-->
        <!--<input placeholder="Email" [(ngModel)] = "email" name="email" required>-->
            <input placeholder="Email" ngModel #txtEmail="ngModel" name="email" required email>
            <!--<p *ngIf="formSignIn.controls.email?.errors?.required">-->
            <p *ngIf="txtEmail.touched && txtEmail.errors?.required">
                Email is required
            </p>
            <p *ngIf="txtEmail.touched && txtEmail.errors?.email">
                Email is not format
            </p>
            <br><br>
            <!--<input type="password" placeholder="Password" ngModel name="password" required>-->
            <input type="password" placeholder="Password" ngModel #txtPassword="ngModel" name="password" required  minlength="6" pattern="[a-z]*">
            <!--<p *ngIf="formSignIn.controls.password?.errors?.required">-->
            <p *ngIf="txtPassword.touched && txtPassword.errors?.required">
                Password is required
            </p>
            <br><br>
            <div ngModelGroup="subjects">
                <label><input type="checkbox" [ngModel]="false" name="NodeJS">NodeJS</label>
                <label><input type="checkbox" [ngModel]="false" name="Angular">Angular</label>
                <label><input type="checkbox" [ngModel]="false" name="ReactJS">ReactJS</label>
            </div>
            <br><br>
            <!--<button>Submit</button>-->
            <button [disabled]="formSignIn.invalid">Submit</button>
            <!-- <button (click)="onSubmit();">Submit</button> -->
        </form>
        <br>
        <!--<button (click)="postToExpress();">POST</button>-->
        <!--<p>{{ txtEmail.errors | json }}</p>-->
        <p>{{ txtPassword.errors | json }}</p>
        <p>{{ formSignIn.value | json }}
    `,
    providers: [SignInService]
})

export class SignInComponent {
    constructor(private signInService: SignInService){}
    // email = '';
    // password = '';
    // onSubmit() {
    //     console.log(this.email, this.password);
    // }
    onSubmit(signInForm) {
        // console.log(signInForm);
        //throw new Error('Form is invalid');
        this.signInService.sendPost(signInForm.value)
        .then(result => console.log(result))
        .catch(err => console.log(err));
    }

    // postToExpress() {
    //     const url = 'http://localhost:3000/signin';
    //     const headers = new Headers({'Content-Type': 'aplication/json'});
    //     const body = JSON.stringify({ name: 'Khoa Pham Trainning'});
    //     this.http.post(url, body, { headers })
    //     .toPromise()
    //     .then(res => res.text())
    //     .then(resText => console.log(resText));
    // }
}



