import { Component, OnInit, ViewChild } from '@angular/core';
import { HelloComponent } from '../hello/hello.component';
import { ListPersonComponent } from '../list-person/list-person.component';

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html',
  styleUrls: ['./user-form.component.css']
})
export class UserFormComponent implements OnInit {

  name = '';
  pass = '';
  loginText = '';
  isHighlight = true;
  evenStyle = {color: 'red', fontSize: '50px'};
  oddStyle = {color: 'black', fontSize: '10px'};
  currentClass = { circle: !this.isHighlight, square: this.isHighlight };

  constructor() { }

  ngOnInit() {
  }

  // showEven(paramUser) {
  //   this.name = paramUser.target.value;
  // }

  login() {
    if(this.name == 'quan' && this.pass == '1234') {
      this.loginText = this.name + ' - ' + this.pass;
      HelloComponent;
    } else {
      this.loginText = 'Đã nhập sai UserName';
    }
  }

}



