import { Component, OnInit } from '@angular/core';
import { WeatherService } from './weather.service';

@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css'],
  providers: [WeatherService]
})
export class WeatherComponent implements OnInit {
  txtCityName = '';
  cityName = '';
  temp = null;
  isLoading = false;

  constructor(private weatheService: WeatherService) { }

  ngOnInit() {
    // this.weatheService.getTemp('Tokyo')
    // .then(temp => console.log(temp))
    // .catch(error => console.error(error)
    // );
  }

  getWeather() {
    this.isLoading = true;
    this.weatheService.getTemp(this.txtCityName)
    .then(temp => {
      this.cityName = this.txtCityName;
      this.temp = temp;
      this.isLoading = false;
      this.txtCityName = '';
    })
    // .catch(err => console.log(err));
    .catch(err => {
      alert('Cannot find you city!');
      this.isLoading = false;
      this.cityName = '';
      this.txtCityName = '';
    });
  }

  getMessage() {
    if(this.isLoading) {
      return 'Loading...';
    }
    return this.cityName === '' ? 'Enter your city name' : (this.cityName + ' is now ' + this.temp)
  }

}



