import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-word',
  templateUrl: './word.component.html',
  styleUrls: ['./word.component.css']
})
export class WordComponent implements OnInit {

  en = '';
  vn = '';
  isShow = false;
  messageError = 'word works!';
  filterStatus = 'XEM_TAT_CA';

  arrWords = [
    { id: 1, en: 'action', vn: 'hành động', memorized: true },
    { id: 2, en: 'actor', vn: 'diễn viên', memorized: false },
    { id: 3, en: 'activity', vn: 'hoạt động', memorized: true },
    { id: 4, en: 'active', vn: 'chủ động', memorized: true },
    { id: 5, en: 'bath', vn: 'tắm', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true }
  ];

  constructor() { }

  ngOnInit() {
  }

  addWord() {
    if(this.en == '') {
      this.messageError = 'You not input English';
    } else {
      if(this.vn == '') {
        this.messageError = 'You not input Vietnamese';
      } else {
        this.arrWords.unshift({
          id: this.arrWords.length + 1, 
          en: this.en, 
          vn: this.vn,
          memorized: true
        });
        // this.arrWords.push({
        //   id: this.arrWords.length + 1, 
        //   en: this.en, 
        //   vn: this.vn,
        //   memorized: true
        // });
        this.en = '';
        this.vn = '';
        this.isShow = false;
      }
    }
  }

  deleteWord(id: number) {
    const index = this.arrWords.findIndex(word => word.id == id);
    this.arrWords.splice(index, 1);
  }

  getShowStatus(memorized: boolean) {
    const dkXemTatCa = this.filterStatus === 'XEM_TAT_CA';
    const dkXemTuDaNho = this.filterStatus === 'XEM_TU_DA_NHO' && memorized;
    const dkXemTuChuaNho = this.filterStatus === 'XEM_TU_CHUA_NHO' && !memorized;
    return dkXemTatCa || dkXemTuDaNho || dkXemTuChuaNho;
  }


}



